.filters, .content {
  padding: 0 1rem; }

.characters {
  margin-left: -1rem;
  margin-right: -1rem; }
  @media (min-width: 1024px) {
    .characters {
      margin-left: 0;
      margin-right: 0;
      display: flex; } }

@media (min-width: 1024px) {
  .filters {
    width: 18.75rem;
    padding: 0 1rem 0 0; } }

@media (min-width: 1024px) {
  .content {
    padding: 0 0 0 1rem;
    width: calc(100% - 18.75rem); } }
